@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
 background: rgb(61, 36, 104);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background:rgb(25, 16, 41);
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
background:rgb(15, 10, 24);

}
@keyframes swipe-in-right {
  0% {
    opacity: 0;
    right: -100px;
  }

  100% {
    opacity: 1;
    right: 0px
  }
}
@keyframes swipe-in-left {
  0% {
    opacity: 0;
    left: -100px;
  }

  100% {
    opacity: 1;
    left: 0px
  }
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
background-image: linear-gradient(83.2deg, rgb(19, 12, 31) 10.8%, rgb(35, 32, 82) 94.3%);
  overflow-x: hidden;
 
  color: rgb(255, 255, 255);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1{
  text-align: center;
}



.chat-container{
  display: flex;
      flex-direction: column;
    margin-bottom: 50px;
      
}

.chat-container > div{
  margin-bottom: 20px;
  max-width: 30%;
  word-break: break-word;
}
/*
.chat-container>div:last-child {
 margin-bottom: 100px;
}
*/

@media (max-width: 767px){
  .chat-container {
    margin-bottom: 250px;
    
  }

}
.user-message{
  color: rgb(220, 220, 220);
  position: relative;
  margin-left: auto;
  margin-right: 20px;
  background: rgb(61, 36, 104);
  
  padding: 20px;
  border-radius: 15px;
  animation: swipe-in-right 500ms ease-in normal;
  
}
.other-message {
  animation: swipe-in-left 500ms ease-in normal;
  position: relative;
  background:rgb(25, 16, 41);
  padding: 20px;
  border-radius: 15px;
  margin-left: 20px;
  margin-right: auto;
}

.chat-bar {
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 10px;
  margin-top: 50px;
  height: 40px;
  position: fixed;
  bottom: 0px;
}
/*
.chat-bar {
  position: relative;
  bottom: 47px;
  width: 100%;
}
*/
#input-message{
  width: 95%;

  margin-left: 20px;
  background: #151515;
  border: none;
  color: white;
  border-radius: 10px;
  height: 100%;
  
}

#input-message:focus{
    outline: none;
    
}

.send-button{
  background-color: rgb(61, 36, 104);
  border: none;
  color: white;
  border-radius: 10px;
  width: 5%;
  height: 100%;
  margin-left: 20px;
  margin-right: 20px;
  font-size: 10px;

}